//
// primitive slider
//

.slider--invalid {
  display: flex;
  align-items: center;

  width: 80%;
  height: 200px;
  margin-bottom: 20px;

  position: relative;
  overflow: hidden;

  .slider__content {
    width: 100%;
    height: 100%;
  }

  .slider__item {
    width: 100%;
    height: 100%;
    display: none;

    &:nth-child(odd) {
      background-color: orange;
    }

    &:nth-child(even) {
      background-color: lightblue;
    }
  }

  .slider__item.active {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 100px
  }

  .slider__arrow {
    font-size: 2em;
    font-weight: 700;
    color: $lcg-1;

    width: 50px;
    height: 50px;
    background-color: transparent;
    outline: 1px solid $lcg-1;
    outline-offset: -1px;

    position: absolute;
    top: 50%;
    transform: translateY(-50%);

    &--previous {
      left: 0;
    }

    &--next {
      right: 0;
    }

    &:hover,
    &:focus {
      color: green;
      background-color: $lcg-1;
    }
  }
}


