.accordeon-1 {
  @include column-flex;
  width: 60%;
  overflow-x: hidden;

  * {
    @include zero-edges;
    width: 100%;
  }

  h3 {
    @include h3-margins;
  }

  .accordeon__btn {
    box-shadow: inset 0 0 15px $mcg-7;

    text-align: start;

    outline: none;
    border: none;
    padding: 1em;
    position: relative;
    cursor: pointer;

    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 50%;
      right: 0;
      width: 0.75em;
      height: 0.15em;
      background-color: currentColor;
      transform: translateY(-50%);
      transition: transform 0.3s ease-in;
    }

    &::before {
      transform: translateX(-100%);
    }

    &::after {
      transform: translateX(-100%) rotate(90deg) ;
    }

    &--active {
      &::before,
      &::after {
        transform: translateX(-100%) rotate(360deg) ;
      }
    }
  }

  .animated {
    animation: 0.3s ease-in;
  }

  .accordeon__content {
    overflow: hidden;

    &:nth-of-type(odd) {
      animation-name: content-x;

      p {
        animation-name: content-x-alternate;
      }
    }

    &:nth-of-type(even) {
      animation-name: content-x-alternate;

      p {
        animation-name: content-x;
      }
    }

    p {
      padding: 1.5em;
      color: $dcg-11;
      background-color: $lcg-1;
      box-shadow: inset 0 0 15px $mcg-8;
      animation: 0.3s ease-in;
    }
  }

  @keyframes content-x {
    0% {
      transform: translateX(-100%);
    }

    100% {
      transform: translateX(0);
    }
  }

  @keyframes content-x-alternate {
    0% {
      transform: translateX(100%);
    }

    100% {
      transform: translateX(0);
    }
  }
}
