html {
  font-size: 10px;
  line-height: 1.15;
  height: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica, Arial, sans-serif;
  font-family: system-ui; // Only supported on Chromium-based browsers and Safari
  font-size: 1.4rem;
  line-height: inherit;
  color: $lcg-1;
  background-color: $mcg-4;
  overflow-wrap: break-word;

  @include zero-edges;
  min-width: $mobile-width;
  min-height: 100vh;

  @include column-flex;
  padding: 50px;

  .list__item {
    font-size: 1.5em;
    line-height: 1;
    margin-bottom: 20px;
    text-align: center;
  }

  .list__link {
    text-decoration: none;
    color: $lcg-1;

    &:hover,
    &:focus {
      color: $mcg-9;
    }
  }
}

.visually-hidden {
  @include visually-hidden;
}

.list {
  @include zero-edges;
  list-style: none;
}

.button {
  border: none;
  outline: none;
}

.link {
  @include link-base-style;
}

.link,
button {
  cursor: pointer;
}
