.navigation {

  .navigation__toggle {
    @media (min-width: $desktop-width) {
      display: none;
    }

    @media (max-width: $pre-desktop-width) {
      outline: none;
      border: none;
      background-color: transparent;

      position: relative;
      width: $toggle-side-size;
      height: $toggle-side-size;
      margin-left: $toggle-margin-left;
      margin-bottom: 30px;
      padding: 0;

      @include column-flex;
      justify-content: space-between;

      &::before,
      &::after {
        content: "";
        @include toggle-child;
      }

      &--active {
        justify-content: center;

        &::before {
          transform-origin: 38%;
          transform: rotate(45deg);
        }

        &::after {
          display: none;
        }

        & > .navigation__toggle-inner {
          transform-origin: 47%;
          transform: rotate(135deg);
        }
      }
    }
  }

  .navigation__toggle-inner {
    @include toggle-child;
  }

  ul {
    margin: 0;
    padding: 15px 25px 15px 40px;
    list-style-type: decimal;

    @media (max-width: $pre-desktop-width) {
      color: $lcg-1;
      background-color: $dcg-11;
      transform: translateX(-100%);
    }

  }

  li {
    &:focus,
    &:hover {
      color: $mcg-4;
    }
  }

  a {
    color: inherit;
    text-decoration: none;

    &:focus,
    &:hover {
      color: currentColor;
      text-decoration: underline;
    }
  }
}

