.carousel {
  width: 60%;
  min-width: 320px;
  max-width: 1000px; // ширина самого узкого изображения
  position: relative;
  margin-bottom: 2.5em;

  color: $dcg-1;
  background-color: $lcg-1;

  box-shadow: 0 0 10px $dcg-3;
  transition: box-shadow 0.2s ease;

  &:focus,
  &:hover {
    box-shadow: 0 0 10px $lcg-1;
  }

  figure {
    @include zero-edges;
    @include column-flex;
    width: 100%;
  }

  img {
    @include content-in-padded-box;
  }

  figcaption {
    font-style: italic;
    padding-bottom: 0.75em;
  }
}

.carousel__inner {
  width: 100%;

  &--js {
    overflow-y: hidden;
  }
}

.carousel__tip > p {
  display: none;
}

.carousel__list {
  display: flex;
  width: 100%;
  outline: none;
  list-style: none;

  overflow-x: auto;
  scroll-snap-type: x mandatory;

  &:focus ~ .carousel__tips .carousel__tip--focus > p,
  &:hover ~ .carousel__tips .carousel__tip--hover > p {
    display: block;
  }

  &:hover ~ .carousel__tips .carousel__tip--focus > p {
    display: none;
  }

  &:focus:hover ~ .carousel__tips .carousel__tip--focus > p,
  &:focus:hover ~ .carousel__tips .carousel__tip--hover > p {
    display: none;
  }

  &:focus:hover ~ .carousel__tips .carousel__tip--focus-and-hover > p {
    display: block;
  }
}

.carousel__item {
  flex-basis: 100%;
  flex-shrink: 0;
  margin-right: 0.5px;

  scroll-snap-align: start;
  scroll-snap-stop: always;
}

.carousel__padded-box {
  @include padded-box;
  padding-bottom: $ratio-carousel;
  margin-bottom: 0.5em;
}

.carousel__dots {
  position: absolute;
  top: 0;
  left: 0;

  display: flex;
  align-items: flex-start;
  padding-top: 0.5em;
  padding-left: 0.5em;
  line-height: 0;
}

.carousel__dot-btn {
  width: 1em;
  height: 1em;
  border: 1px solid $lcg-1;
  border-radius: 50%;
  outline: none;
  background-color: transparent;
  cursor: pointer;

  @include zero-edges;

  &:not(:last-child) {
    margin-right: 0.6em;
  }

  &.active {
    background-color: $lcg-1;
  }

  &:focus,
  &:hover {
    box-shadow: 0 0 5px $lcg-1;
  }
}

.carousel__arrow-btn {
  @include zero-edges;
  @include centering-flex;

  outline: none;
  border: none;
  background-color: transparent;

  position: absolute;
  top: 50%;
  transform: translateY(-50%);

  width: 2.5em;
  height: 2.5em;
  border-radius: 50%;
  box-shadow: none;

  transition: all 0.3s ease-in;
  cursor: pointer;

  &:before {
    content: "";
    display: block;
    width: 30%;
    height: 30%;
    border: 0.2em solid $lcg-1;
    border-right-color: transparent;
    border-bottom-color: transparent;
  }

  &--previous {
    left: 0;

     &:before {
      transform: translateX(0.11em) rotate(-45deg);
    }
  }

  &--next {
    left: 100%;
    transform: translate(-100%, -50%);

    &:before {
      transform: translateX(-0.15em) rotate(135deg);
    }
  }

  &:focus,
  &:hover,
  &:active {
     box-shadow: inset 0 0 0.5em $lcg-1;
  }
}
